.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
  font-family: 'Roboto', sans-serif;
}

.infected {
  border-bottom: 10px solid #fbc531;
}

.recovered{
  border-bottom: 10px solid #55efc4;
}

.deaths{
  border-bottom: 10px solid #e84118;
}

@media only screen and (max-width: 1000px) {
  .card {
    margin: 2% 2% !important;
    text-align: center;
  }
}