@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
body {
  background-color: #8c7ae6;
  font-family: 'Roboto', sans-serif;
}

.container {
  display: flex;
  
  flex-direction: column;
}

.centered{
  
  text-align: center;
  
}

.chart{
  margin: auto ;
}
.image {
  width: 370px;
  margin-top: 50px;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}
